/* mobile */

.footer {
    margin: 0 auto;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 160px;
    flex-wrap: wrap;
    padding: 60px;
    box-sizing: border-box;
    padding-left: 10px;
    padding-right: 10px;
}

.footer p {
    color: rgb(201, 201, 201);
    padding-bottom: 60px;
    transition: 2s;
}

.footer p:hover {
    color: rgb(35, 35, 35);
    transition: 2s;
}

.footer__social-list {
    list-style: none;
    display: flex;
    justify-content: space-between;
    margin: 0;
    padding: 0;
    margin-top: 30px;
    margin-bottom: 30px;
    width: 80%;
}

.footer__social-item img {
    width: 40px;
    /* margin-right: 20px; */
}

.footer__social-item img:hover {
    transform: scale(1.2);
    transition: 0.5s;
}


/* tablet */

@media (min-width: 700px) {
    .footer {
        width: 700px;
    }
    .footer__social-item img {
        width: 60px;
        /* margin-right: 40px; */
    }
}


/* desctop */

@media (min-width: 1200px) {
    .footer {
        width: 1200px;
    }
    .footer__social-item img {
        /* margin-right: 60px; */
    }
}