/* mobile  */

.title {
    width: 100%;
    padding: 0;
    height: 800px;
    background-image: url(../../img/nesvizh.png);
    position: relative;
    top: 0px;
    /* z-index: 0; */
    color: white;
    font-family: 'Open Sans';
    display: flex;
    align-items: center;
}

.title__wrapp {
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 360px;
    flex-wrap: wrap;
}

.title__img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin: 0;
    padding: 0;
}

.title__name {
    font-size: 40px;
    text-align: center;
}

.title__name,
.title__description {
    padding: 0;
    margin: 0;
    text-transform: uppercase;
    mix-blend-mode: overlay;
    transition: 2s linear;
    cursor: pointer;
}

.title__name:hover,
.title__description:hover {
    mix-blend-mode: exclusion;
    transition: 2s linear;
    cursor: pointer;
}

.title__description {
    font-size: 30px;
    margin-bottom: 20px;
}


/* tablet */

@media (min-width: 700px) {
    .title {
        height: 700px;
    }
    .title__wrapp {
        justify-content: space-between;
        flex-wrap: nowrap;
        padding-top: 110px;
    }
    .title__img {
        width: 300px;
        height: 300px;
        margin-left: 25px;
    }
    .title__name {
        font-size: 60px;
    }
    .title__description {
        margin-bottom: 0;
    }
}


/* desctop */

@media (min-width: 1200px) {
    .title__img {
        width: 400px;
        height: 400px;
        margin-left: 50px;
    }
    .title__name {
        font-size: 70px;
    }
}


