/* mobile  */

.about {
    margin: 0 auto;
    width: 100%;
    margin-top: 60px;
    font-size: 18px;
    padding-left: 10px;
    padding-right: 10px;
}

.about__title {
    font-size: 30px;
    font-weight: bold;
    color: rgb(53, 53, 53);
    border-bottom: 1px solid rgb(53, 53, 53);
    padding-bottom: 30px;
}

.about_list {
    width: 240px;
    margin-right: 40px;
    margin: 0 auto
}

.about__photo {
    width: 240px;
    border-radius: 20px;
    margin: 0 auto
}

.about__content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.about__text {
    text-indent: 20px;
    margin: 0;
    padding: 0;
    color: rgb(1, 1, 36);
}

.about__text--i {
    font-style: italic;
}

.about__text--red {
    color: brown;
}

.about__list span,
.about__text span {
    color: black;
}

.about__list {
    list-style: none;
    padding: 0;
    margin: 0;
    padding-left: 24px;
    color: rgb(1, 1, 36);
    margin: 0 auto
}

.about__list li {
    position: relative;
}

.about__list li::after {
    position: absolute;
    content: "";
    width: 16px;
    height: 2px;
    background-color: rgb(28, 29, 86);
    top: 13px;
    left: -25px;
}


/* tablet */

@media (min-width: 700px) {
    .about__title {
        font-size: 40px;
    }
    .about {
        padding-left: 20px;
        padding-right: 20px;
    }
    .about__content {
        justify-content: space-between;
        flex-wrap: nowrap;
        align-items: flex-start;
    }
    .about__text--wrapp {
        margin-left: 20px;
        margin-right: 20px;
    }
}


/* desctop */

@media (min-width: 1200px) {
    .about__title {
        font-size: 50px;
    }
    .about {
        width: 1200px;
    }
    .about__content {
        padding-left: 0;
        padding-right: 0;
    }
}