/* mobile */

.nav__wrapp {
    /* margin: 0 auto; */
    width: 100%;
    background-color: rgba(0, 0, 0, 30%);
    position: absolute;
    content: "";
    z-index: 5;
}

.nav {
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.nav__list {
    display: flex;
    list-style: none;
    justify-content: center;
    flex-wrap: wrap;
    /* margin-left: 175px; */
    width: 100%;
    padding: 0;
    text-align: center;
    margin-bottom: 0;
}

.nav__list li a {
    color: white;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    text-decoration: none;
    /* margin-right: 79px; */
    position: relative;
}

.nav__list li a::after {
    position: absolute;
    content: "";
    bottom: -10px;
    left: 0;
    width: 0;
    height: 0;
    background-color: white;
    transition: 1s linear;
}

.nav__list li a:hover::after {
    width: 100%;
    height: 2px;
    transition: 1s;
}

@media (max-width: 699px) {
    .nav__list li {
        /* background-color: rgba(11, 185, 40, 0.632); */
        align-content: center;
        /* color: black; */
        width: 100%;
        height: 50px;
        padding-top: 20px;
        border-bottom: 1px solid white;
    }
    .nav__list li:nth-child(1) {
        border-top: 1px solid white;
    }
}


/* tablet */

@media (min-width: 700px) {
    .nav__wrapp {
        height: 110px;
        justify-content: space-between;
    }
    .nav {
        /* width: 400px; */
        flex-wrap: nowrap;
        justify-content: space-between;
        height: 100%;
        z-index: 3;
    }
    .nav__list {
        flex-wrap: nowrap;
        justify-content: flex-end;
    }
    .nav__list li a {
        margin-right: 40px;
    }
}


/* desktop */

@media (min-width: 1200px) {
    .nav {
        width: 1200px;
    }
    .nav__list li a {
        margin-right: 79px;
    }
}