/* mobile */

.skills {
    margin: 0 auto;
    width: 100%;
    margin-top: 60px;
    padding-left: 10px;
    padding-right: 10px;
}

.skills-title {
    font-size: 30px;
    font-weight: bold;
    color: rgb(53, 53, 53);
    border-bottom: 1px solid rgb(53, 53, 53);
    padding-bottom: 30px;
}

.skill-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    list-style: none;
    margin: 0;
    padding: 0;
}

.skill-item {
    display: flex;
    width: 100%;
    margin-bottom: 40px;
    align-items: center;
    color: rgb(53, 53, 53);
    justify-content: center;
    flex-wrap: wrap;
}

.skill-item__image {
    width: 100px;
    margin-right: 0;
    padding-top: 7px;
    margin-bottom: 20px;
}

.skill-item__title {
    font-weight: bold;
    font-size: 20px;
    margin: 0;
    padding: 0;
}


/* tablet */

@media (min-width: 700px) {
    .skills {
        padding-left: 20px;
        padding-right: 20px;
    }
    .skills-title {
        font-size: 40px;
    }
    .skill-item {
        display: flex;
        width: 380px;
        margin-bottom: 40px;
        align-items: flex-start;
        flex-wrap: nowrap;
    }
    .skill-item__image {
        width: 120px;
        margin-right: 20px;
    }
}


/* desctop */

@media (min-width: 1200px) {
    .skills {
        width: 1200px;
        padding-left: 0;
        padding-right: 0;
    }
    .skills-title {
        font-size: 50px;
    }
    .skill-item {
        width: 380px;
    }
    .skill-item__image {
        width: 140px;
    }
}