/* mobole */

.portfolio__wrapp {
    background-color: rgb(53, 53, 53);
    background-image: url(../img/nesvizh.png);
    width: 100%;
    height: 600px;
}

.portfolio {
    margin: 0 auto;
    width: 100%;
    margin-top: 110px;
    font-size: 18px;
    padding: 0;
    padding-top: 5px;
    padding-left: 10px;
    padding-right: 10px;
}

.portfolio__title {
    font-size: 30px;
    font-weight: bold;
    color: white;
    border-bottom: 1px solid white;
    padding-bottom: 30px;
}

.portfolio__sliders {
    width: 100%;
    text-align: center;
    margin: 0 auto
}

.portfolio__slider {
    width: 300px;
    border-radius: 10px;
    padding: 0;
    text-align: center;
}

.portfolio__slide--grid {
    display: grid;
    grid-template: 1fr, 1fr / 1fr;
}

.portfolio__img {
    padding: 0;
    margin: 0;
    background-color: rgba(31, 64, 88, 1);
    width: 300px;
    margin: 0 auto;
    z-index: 3;
    grid-column: 1;
    grid-row: 1;
}

.portfolio__img:hover {
    -webkit-box-shadow: 0px 5px 32px -4px rgba(255, 255, 255, 0.47);
    -moz-box-shadow: 0px 5px 32px -4px rgba(255, 255, 255, 0.47);
    box-shadow: 0px 5px 32px -4px rgba(255, 255, 255, 0.47);
}

.portfolio__popup {
    opacity: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 300px;
    height: 270px;
    background: rgb(29, 65, 129);
    background: radial-gradient(circle, rgba(29, 65, 129, 1) 0%, rgba(23, 51, 101, 1) 100%);
    border-bottom-right-radius: 3px;
    text-align: center;
    transition: 3s;
    padding: 20px;
    margin: 0 auto;
    margin-top: 186px;
    z-index: 4;
    grid-column: 1;
    grid-row: 1;
}

.portfolio__popup:hover {
    transition: 1s;
    opacity: 1;
}

.portfolio__link {
    line-height: 18px;
    background-color: #277661;
    display: inline-block;
    padding: 16px 40px;
    border: 0;
    border-radius: 3px;
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    transition: 2s;
}

.portfolio__link:hover {
    background-color: white;
    color: rgb(62, 62, 62);
    -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
    transition: 2s;
}

.portfolio__link:active {
    background-color: brown;
    color: rgb(149, 149, 149);
}

.portfolio__title--popap {
    font-weight: bold;
    font-size: 18px;
    line-height: 30px;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    text-align: center;
    margin-top: 26px;
    text-transform: uppercase;
    color: #ddd4b7;
    position: relative;
    transition: 2s;
    padding-bottom: 20px;
}

.portfolio__title--popap:hover {
    color: rgb(255, 255, 255);
    transition: 2s;
}

.portfolio__title--popap::after {
    position: absolute;
    content: "";
    width: 160px;
    height: 2px;
    background-color: #ddd4b7;
    bottom: -10px;
    left: 20%;
}

.portfolio__desc--popap {
    color: white;
    padding-top: 0;
    min-height: 65px;
}


/* tablet */

@media (min-width: 700px) {
    .portfolio {
        padding-left: 20px;
        padding-right: 20px;
    }
    .portfolio__slider {
        width: 360px;
    }
    .portfolio__img {
        width: 360px;
    }
    .portfolio__popup {
        width: 360px;
    }
    .portfolio__title {
        font-size: 40px;
    }
    .portfolio__wrapp {
        height: 800px;
    }
    .portfolio__popup {
        height: 270px;
        margin-top: 270px;
    }
    .portfolio__title--popap::after {
        left: 25%;
    }
}


/* desctop */

@media (min-width: 1200px) {
    .portfolio {
        width: 1200px;
        padding-left: 0;
        padding-right: 0;
    }
    .portfolio__title {
        font-size: 50px;
    }
}